.navContainer{
    background-color: #000000;
    border: 1px solid black;
    display: flex;
    justify-content: space-evenly;
    height: 3vw;
    align-items: center;
    opacity: .9;
    border-bottom: white solid 2px;
    z-index: -1;
}

.navLinks{
    color: #FFF;
    font-weight: bold;
    padding-left: 5px;
    padding-right: 5px;
    text-decoration: none;
    border-bottom: #FFF .2vw solid;
    font-size: 1.5vw;
    -webkit-transition: .3s ease-out;
    transition: .3s ease-out;
}

.navLinks:hover{
    font-size: 2vw;
    -webkit-transition: .3s ease-out;
    transition: .3s ease-out;
}

/* Mobile View */
